
.main--header {
	position: relative;
	z-index: 3;
	top: 0;
	left: 0;

	width: 100%;

	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;

	background: linear-gradient( to bottom, rgba(#000, 0.25), rgba(#000, 0.0) );

	@include mq-md {
		padding: 2em 4em;
	}

	@include mq-sm {
		padding: 1em 2em;
	}

}

.main--logo svg {
	fill: #fff;
	width: 5em;

	position: relative;
	z-index: 6;
}

//  Navigation
// -------------

.main--nav__icon svg {
	fill: #fff;
	width: 2em;
}

.main--nav__close {
	position: absolute;

	@include mq-md {
		top: 2.8em;
		right: 4em;
	}
	@include mq-sm {
		top: 2em;
		right: 2em;
	}

	svg {
		fill: $color-dark;
		width: 2em;
	}
}

.main--nav__list {
	position: fixed;
	top: 0;
	height: 100%;
	z-index: 5;

	background-color: #fff;
	padding: 8em 2em;
	box-shadow: 0 -5px 15px 4px rgba(#000, 0.1);

	color: $color-dark;
	white-space: nowrap;

	@include mq-md {
		width: 284px;

		// hide on desktop
		right: -284px;
	}
	@include mq-sm {
		width: 94%;

		// hide on mobile
		right: -94%;
	}

// hide state --------
	transition: 0.2s ease-in;

// show state ---------
	&.-is--open {
		right: 0;
	}

}

	.main--nav__item {
		display: block;
		padding: 0.05em 0 0.05em 0.25em;
		margin-bottom: 0.5em;

		font-size: 1.2em;

		border-left: 0em solid transparent;
		transition: 0.2s;

		&:hover {
			border-left: 0.25em solid $color-cta;
		}
	}
